import React from 'react'
import { Container, Menu } from 'semantic-ui-react'

const AppMenu = ({sort, sortName, shownName}) => {

    return (
        <Container>
            <Menu borderless size='tiny' fluid widths={3}>
                <Menu.Item header><i className="large find icon"></i> 
                    SIGHT-A-DUCK
                </Menu.Item>
                <Menu.Item link onClick={sort()}>
                    <i className={sortName}></i>
                </Menu.Item>
                <Menu.Item>
                    VINCIT SUMMER 2018 KOODIJAHTI
                </Menu.Item>
            </Menu>
        </Container>
    )
}

export default AppMenu


